.carousel-section {
  margin-top: 30px;
  color: black;
  padding: 20px;
}

.carousel-section h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.carousel-container {
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
}

.carousel-content {
  display: flex;
  overflow-x: hidden;
  scroll-behavior: smooth;
  width: 80%;
}

.carousel-item {
  min-width: 300px;
  margin-right: 10px;
  background-color: #333;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
  height: 240px;
}

/* 화살표 버튼 공통 스타일 */
.carousel-button {
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.prev {
  left: 75px;
}

.next {
  right: 75px;
}
